export default{
	data:function(){
		return {
			catid:0,
			pageLoad:false,
			pageData:{},
			pageCatid:0
		}
	},
	onLoad:function(ops){
		this.catid=ops.catid
		this.getPage();
	},
	methods:{
		setCat:function(catid){
			this.catid=catid;
			this.getList();
		},
		goProduct:function(id,shopid){
			uni.navigateTo({
				url:"../b2b_product/show?id="+id+"&shopid="+shopid
			})
		},
		getPage:function(){
			var that=this;
			that.app.get({
				url:that.app.apiHost+"/module.php?m=b2b_product&a=list&ajax=1&catid="+that.catid,
				success:function(res){
					that.pageData=res.data;
					that.pageLoad=true;
					if(that.pageCatid==0){
						that.pageCatid=res.data.cat.catid;
					}
					
					uni.setNavigationBarTitle({
						title:res.data.cat.title
					})
				}
			})
		},
		getList:function(){
			var that=this;
			that.app.get({
				url:that.app.apiHost+"/module.php?m=b2b_product&a=list&ajax=1&catid="+that.catid,
				success:function(res){
					that.pageData.list=res.data.list;
					that.pageLoad=true;
					 
					uni.setNavigationBarTitle({
						title:res.data.cat.title
					})
				}
			})
		},
	}
}
